/* stylelint-enable */
:root {
  --transparent: transparent;
  --black: #000000;
  --background: #131314;
  --dark: #1c1c1c;
  --white-02: rgba(255, 255, 255, 0.2);
  --white: #ffffff;
  --gray-light: #c9ccd4;
  --aqua: #0ad9c6; }

*,
*::before,
*::after {
  box-sizing: border-box; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.container {
  max-width: 1440px;
  min-width: 320px;
  margin: 0 auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.list {
  margin: 0;
  padding: 0;
  list-style: none; }

.text {
  margin: 0; }

.image {
  vertical-align: middle; }

.page {
  font-family: "Open Sans", "Arial", sans-serif;
  scroll-behavior: smooth; }

.page__body {
  display: grid;
  height: 100vh;
  margin: 0; }

.header {
  min-height: 825px;
  letter-spacing: -0.005em;
  color: var(--white);
  background-color: var(--background);
  background-image: url("../img/banner-bike-desktop.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  margin-bottom: 400px; }
  @media (min-resolution: 2dppx) {
    .header {
      background-image: url("../img/banner-bike-desktop@2x.jpg"); } }

.header__container {
  padding-left: 280px;
  padding-right: 280px; }

.navigation {
  min-height: 62px; }

.navigation__list {
  display: flex;
  padding: 20px 20px 10px 20px;
  border-bottom: 1px solid var(--white-02); }

.navigation__item:not(:last-child) {
  margin-right: 135px; }

.navigation__item a {
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  color: var(--white);
  opacity: 0.7; }

.promo {
  display: flex;
  justify-content: space-between;
  padding-top: 256px; }

.promo__block-text {
  max-width: 400px;
  margin-right: 15px; }

.promo__block-text a {
  display: flex; }

.promo__block-text h2 {
  margin-top: 30px;
  font-weight: bold;
  font-size: 46px;
  line-height: 64px; }

.promo__block-text p {
  margin-top: 24px;
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  opacity: 0.8; }

.promo__block-form {
  width: 327px;
  margin-left: 15px; }

.promo__block-form p {
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  text-align-last: center; }

.form input {
  margin-bottom: 20px;
  padding: 13px 30px;
  width: 100%;
  height: 48px;
  font-size: 15px;
  line-height: 24px;
  color: var(--dark);
  background-color: var(--white);
  border: 1px solid var(--gray-light);
  border-radius: 6px; }
  .form input::placeholder {
    opacity: 0.5; }

.form button {
  width: 100%;
  min-height: 51px;
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  color: var(--black);
  background-color: var(--aqua);
  border-radius: 6px; }
